header{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0.5rem;
    border: solid 1px rgba(0, 0, 0, 0.116);
}
img{
    height: 8rem;
}
.btn-one {
    margin: 2rem;
    text-align: center;
    padding: 0.5rem 1rem;
    color: black;
    background-color: #734a9153;
    text-decoration: none;
    font-family: 'Cinzel';
    font-size: 0.8rem;
    transition: all 0.3s;
    position: relative;
}
.btn-one::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0;
    transition: all 0.3s;
    border-top: 1.5px solid #734a91;
    border-bottom: 1.5px solid #734a91;
    -webkit-transform: scale(0.1, 1);
            transform: scale(0.1, 1);
}
.btn-one:hover{
    letter-spacing: 1.5px;
}
.btn-one:hover::before {
    opacity: 1; 
    -webkit-transform: scale(1, 1); 
            transform: scale(1, 1); 
}
.nombreEmpresa{
    font-size: 3.5rem;
    font-family: 'Cinzel';
    letter-spacing: 5px;
}

@media only screen and (max-width: 1000px) {
    header {
        flex-direction: column;
    }
    .nombreEmpresa{
        font-size: 2rem;
        margin: 1rem;
    }
    img{
        height: 6rem;
    }
    .btn-one{
        font-size: 0.8rem;
    }
}

@media only screen and (max-width: 670px) {

    .nombreEmpresa{
        display: none;
    }
    ul{
        display: flex;
        margin: 0;
        padding: 0 !important;
        flex-direction: column;
    }
    .btn-one{
        font-size: 0.8rem;
        margin: 1px;
    }
}

.producto{
    display: flex;
    flex-direction: column;
    width: 16rem;
    height: auto;
    margin: 1.5rem;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    border: solid 1px rgba(75, 72, 72, 0.073);
    border-radius: 1rem;
    background-color: rgba(211, 188, 246,0.8);
    box-shadow: -5px 7px 15px 0px rgba(0,0,0,0.81);
}
.fotoProducto{
    width: 100%;
    height: auto;
    background-color: white;
}

.tituloProducto{
    text-shadow: 2px 2px 2px #bc98f3;
    text-align: center;
    font-size: 1.5rem;
    padding-top: 1rem;
}

.producto button{
    margin: 1rem;
}

.precio{
    position: relative;
    background: #b57edc;
    width: 16.46rem;
    margin-right: -0.65rem;
    padding: .2rem 1.2rem;
    border-radius: 5px 0 0 5px;
}
.precio p {
    margin: 0 0 0 4rem;
    /* padding-top: .4rem; */
    display: flex;
    text-align: center;
    align-items: center;
    align-content: center;
    font-size: 1.6rem;
    font-weight: 500;
    }

    .precio p:before {
        content: "$";
        margin-right: 5px;
        font-size: 15px;
        font-weight: 300;
    }

    .precio div {
        position: absolute;
        bottom: -23px;
        right: 0px;
        width: 0;
        height: 0;
        border-top: 13px solid #6f56a0;
        border-bottom: 10px solid transparent;
        border-right: 13px solid transparent;
        z-index: -6;
    }


.fancy {
    background-color: transparent;
    border: 2px solid #8f7193;
    border-radius: 0;
    box-sizing: border-box;
    color: #e5dde6;
    cursor: pointer;
    display: inline-block;
    font-weight: 700;
    letter-spacing: 0.05em;
    margin: 0;
    outline: none;
    overflow: visible;
    padding: 0.5rem 1.8rem;
    position: relative;
    text-align: center;
    text-decoration: none;
    text-transform: none;
    transition: all 0.3s ease-in-out;
    -webkit-user-select: none;
            user-select: none;
    font-size: 13px;
    margin:1rem;
    }
    
    .fancy::before {
        content: " ";
        width: 1.5625rem;
        height: 2px;
        background: #503459;
        top: 50%;
        left: 1.5em;
        position: absolute;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        -webkit-transform-origin: center;
                transform-origin: center;
        transition: background 0.3s linear, width 0.3s linear;
    }
    
    .fancy .text {
        font-size: 0.6rem;
        line-height: 1rem;
        padding-left: 2em;
        display: block;
        text-align: left;
        transition: all 0.3s ease-in-out;
        text-transform: uppercase;
        text-decoration: none;
        color: #503459;
    }
    
    .fancy .top-key {
        height: 2px;
        width: 1.5625rem;
        top: -2px;
        left: 0.625rem;
        position: absolute;
        background: #e8e8e8;
        transition: width 0.5s ease-out, left 0.3s ease-out;
    }
    
    .fancy .bottom-key-1 {
        height: 2px;
        width: 1.5625rem;
        right: 1.875rem;
        bottom: -2px;
        position: absolute;
        background: #e8e8e8;
        transition: width 0.5s ease-out, right 0.3s ease-out;
    }
    
    .fancy .bottom-key-2 {
        height: 2px;
        width: 0.625rem;
        right: 0.625rem;
        bottom: -2px;
        position: absolute;
        background: #e8e8e8;
        transition: width 0.5s ease-out, right 0.3s ease-out;
    }
    
    .fancy:hover {
        color: #e5dde6;
        background: #503459;
    }
    
    .fancy:hover::before {
        width: 0.9375rem;
        background: #e5dde6;
    }
    
    .fancy:hover .text {
        color: #e5dde6;
        padding-left: 1.5em;
    }
    
    .fancy:hover .top-key {
        left: -2px;
        width: 0px;
    }
    
    .fancy:hover .bottom-key-1,
        .fancy:hover .bottom-key-2 {
        right: 0;
        width: 0;
    }
.cardContainer{
    width: auto;
    display: flex;  
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 1rem;
}
.contadorProductos{
    width: 10rem;
    display: flex;
    flex-direction: column;
    
}
.contador{
    display: flex;
    justify-content: space-between;
    height: 1.5rem;
    margin-bottom: 4px;
}
.contador p{
    margin: 0;
}
.contador button{
    display: flex;
    align-items: center;
    padding: 0 0.5rem;
}
button{
    border: solid 1px black;
}
.detailContainer{
    display: grid;
    grid-template-areas: 
    "titulo titulo"
    "imagen descripcion"
    "imagen precio"
    "imagen contador"
    ;
    grid-gap: 2rem;
    gap: 2rem;
    justify-items: center;
    align-items: center;
}

.titulo{
    grid-area: titulo;
}
.imagen{
    grid-area: imagen;
    width: 90%;
    height: auto;
    box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.65);
    border-radius: 2rem;
}

.descripcion{
    grid-area: descripcion;
}


.precio{
    grid-area: precio;
    text-align: center;
}

.contador{
    grid-area: contador;
}
.container{
    width: auto;
    display: flex;  
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 1rem;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #9599E2;
  background-image: linear-gradient(135deg, #9599E2 0%,  #dbb6ee 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

